import { Component, OnInit, OnDestroy } from '@angular/core';
import { ZipCodeService } from '../shared/services/zipcode.service';
import { ZipCodeResult } from '../shared/models/zipcode.model';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})
export class MapComponent implements OnInit, OnDestroy {

  title: string = 'Map';
  lat: number = 52.2373951689151;
  lng: number = 6.8435707307834;

  sub: Subscription;

  constructor(private zipCodeService: ZipCodeService) { }

  ngOnInit() {
    this.sub = this.zipCodeService.Stream.subscribe((res => {
      if (res && res.details && res.details.length > 0) {
        this.lat = parseFloat(res.details[0].lat);
        this.lng = parseFloat(res.details[0].lon);

        console.log("New lat/long", this.lat, this.lng);
      }
    }));
  }

  ngOnDestroy(): void {
    this.sub.unsubscribe();
  }
}



