import { Component, OnInit, Output, EventEmitter} from '@angular/core';
import { Address } from '../shared/models/address.model';
import { ZipCodeService } from '../shared/services/zipcode.service';
import { ZipCodeResult } from '../shared/models/zipcode.model';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {
  currentAddress: Address;
  currentResult$: Observable<ZipCodeResult>;
  hasResult: boolean;

  constructor(private zipCodeService : ZipCodeService) { }

  ngOnInit() {
    this.zipCodeService.Stream.subscribe((next) => {
      this.hasResult = (next != null && next.details != null && next.details.length>0);
    });
  }

  getAddress(zipcode: HTMLInputElement, housenumber: HTMLInputElement) {
    this.currentAddress = {
      zipcode: zipcode.value,
      housenumber: housenumber.value
    };
    this.zipCodeService.search(this.currentAddress);
    console.log(`Search request : ${this.currentAddress.zipcode} - ${this.currentAddress.housenumber}`);
  }

  clearAddress(zipcode: HTMLInputElement, place: HTMLInputElement) {
    zipcode.value = '';
    place.value = '';
    this.currentAddress = null;
    this.zipCodeService.clear();
    console.log(`Cleared the form`);
  }
}



