import { Injectable, Inject, Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import { ZipCodeResult } from '../models/zipcode.model';
import { environment } from '../../../environments/environment.prod';
import { Address } from '../models/address.model';
import { Subject } from 'rxjs/Subject';

// This is a shared service
@Injectable()
export class ZipCodeService implements OnInit {

  Stream: Subject<ZipCodeResult> = new Subject<ZipCodeResult>();

  search(address: Address){
    let serviceURL: string = `${environment.zipcodeurl}?postcode=${address.zipcode}&streetnumber=${address.housenumber}&ref=localhost`;
    let request = this.httpClient.get<ZipCodeResult>(serviceURL);
    request.subscribe((next: ZipCodeResult) => {
      this.Stream.next(next);
      console.log(`Response retreived from ZipCode API`, next);
    },
      (error: any) => { console.error("Error in searching ZipCode API"); },
      () => { console.log("Request completed!"); }
    );
  }

  clear() {
    this.Stream.next();
  }

  constructor(private httpClient: HttpClient) {

  }

  ngOnInit() {
  }

  private handleError(error: any) {
     let errMsg = (error.message) ? error.message :
      error.status ? `${error.status} - ${error.statusText}` : 'Server error';
    console.error(errMsg); // log to console instead
    return Observable.throw(errMsg);
  }
}



