import { Component, OnInit, Input, OnChanges, OnDestroy } from '@angular/core';
import { Address } from '../shared/models/address.model';
import { Observable } from 'rxjs';
import { ZipCodeService } from '../shared/services/zipcode.service';
import { ZipCodeResult } from '../shared/models/zipcode.model';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-results',
  templateUrl: './results.component.html',
  styleUrls: ['./results.component.css']
})
export class ResultsComponent implements OnInit, OnDestroy {
  @Input() zipCodeResult: ZipCodeResult;

  street: string;
  city: string;
  province: string;
  hasResult: boolean;
  isFirstResult: boolean = true;

  sub: Subscription;

  constructor(private zipCodeService: ZipCodeService) { }

  ngOnInit(): void {
    this.sub = this.zipCodeService.Stream.subscribe((res => {
      this.isFirstResult = false;
      if (res && res.details && res.details.length > 0) {
        this.hasResult = true;
        this.street = res.details[0].street;
        this.city = res.details[0].city;
        this.province = res.details[0].province;
      } else {
        this.hasResult = false;
      }

    }));
  }

  ngOnDestroy(): void {
    this.sub.unsubscribe();
  }

}



